	module.exports = {"data":{"markdownRemark":{"html":"<p>\"큰\"응용 프로그램을 구성하기 위해서는 완성 된 모든 양식에서 획기적인 응용 프로그램 일 것입니다. 일본 국제 포럼에서 상반기에 전달 된 원고를 찾으십시오.이 양식은 부록에 그 내용을 포함시켜야합니다. 그것을 다시 읽고 안에 적는다.</p>\n<p>개혁과 개방의 정책은 중국 사회와 중국 국가의 가장 기본적인 이익을 대표하는 것으로, 개혁과 개방이 중국 사회 현대화를위한 유일한 길임을 증명했다. 현대 사회에서는 중국이 사회 경제에서 뒤쳐져 있으며 세계의 일반적인 추세에 뒤쳐져 있습니다. 경제적 후진성으로 인해 중국은 항상 국제 관계에서 수동적이었으며, 근대부터 중국 국가는 어려움과 전쟁을 겪었다. 이 상황은 중국의 국제 지위에 영향을주고 중국의 사회 건강 발전에 영향을 미치며 사람들의 일상 생활에도 영향을 미쳤습니다. 중화 인민 공화국 건국 이후 경제력은 급속도로 발전했지만 모든 사람들에게 알려진 이유 때문에 경제적 발전과 사회적 발전은 항상 사회 발전의 중심이되지 못했습니다. 중국은 역사적, 실용적 발전의 필요성을 통해 개혁의 길을 열어 놓았으며, 개혁과 개방은 중국 국민의 가장 기본적인 이익을 대표하며 중국 전체의 가장 기본적인 이해 관계를 나타 내기 때문에 역사적인 방향이다. 그것은 일반적인 선택입니다.</p>\n<p>개혁 개방의 정책은 중국 국민들의 열렬한지지를 얻었습니다. 개혁과 개방의 정책은 중국 역사상 가장 급속한 발전을 가져 왔으며 중국의 사회 경제적 개발은 전례없는 변화를 겪었으며 전반적인 국가의 힘은 크게 개선되었고 경제력은 크게 증가했으며 국민의 생활 수준은 빠르게 향상되었습니다. 이 모든 것은 국민의 마음 속에 뿌리 깊게 자리 잡고 있으며 전 국민의 진실 된지지와지지를 얻었습니다. 마음의 바닥에 사는 사람들은 개혁 개방의 정책을 받아들입니다. 덩 샤오핑 (Deng Xiaoping)은 \"당신이 사회주의를 고수하지 않으면 개혁 개방을하지 않고 경제를 발전시키지 않고 국민의 삶을 개선하지 않으면 막 다른 길을 걸을 수있다\"면서 \"제 3 기 본회의 이후의 노선과 원칙, 정책을 바꾸려면 사람들은 동의하지 않는다. 전복 될 것 \"이라고 말했다. 개혁 개방의 정책은 이미 국민들과 국민의 의도에 대한 대중적 열망이다.</p>\n<p>경제 시스템과 사회 시스템은 새로운 메커니즘을 도입했습니다. 수년간의 개혁과 개방 이후 중국은 점차 새로운 경제 및 사회 체제를 확립했으며 새로운 메커니즘에서 다양한 사회 세력과 요소가 새로운 조합을 형성했습니다. 이 새로운 메커니즘에서 국가 경제와 사회 관리의 유통과 운영은 이미 임의로 변경 될 수없는 논리이며, 그렇지 않으면 전체 국가 경제와 사회 관리가 심각하게 기울어 져 심각한 결과를 가져올 수 있습니다. 중국 사회의 사회 자원 분배, 정부의 거시 관리, 경제 운영 모델, 경제 구조, 국내외 경제 관계의 결합, 다양한 사회 활동의 조정 등은 모두 기존 시스템과 크게 다른 시스템을 형성합니다. 이것은 그 자체로 개혁과 개방을위한 강력한 보증입니다.</p>\n<p>중국 지도층은 개혁 개방의 길을 높이 인식하고있다. 중국의 현재 간부반에서는 중앙 간부와 지방 간부 모두 비전, 경험, 지식 구조 및 가치 지향에서 중요한 변화를 겪어 왔습니다. 근대화와 개혁과 개방은 이미 그들의 마음 속에 확고한 목표를 구성했습니다. 그들 스스로는 세계에 대해 더 깊이 이해하고 있으며, 인간 개발을위한보다 열린 아이디어를 가지고 있으며, 현대 과학 지식을 더 잘 파악하고, 국가 경영에 대한보다 현대적인 인식을 가지고 있으며, 경제 및 사회 발전을 진전시키는 데보다 강력한 영향을 미칩니다. 추적. 이것은 중국의 근대화와 개혁 개방의 기본 조직 보증입니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0193.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0193.md","frontmatter":{"title":"11월 24일 목요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0193.md absPath of file >>> MarkdownRemark"}}

